import { Injectable } from '@angular/core';
import {MessageModel} from './models/message.model';
import {HttpClient, HttpParams} from '@angular/common/http';
import {LoginService} from './login.service';
import {RoomModel} from './models/room.model';
import {switchMap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  constructor(
    private http: HttpClient,
  ) { }

  getMessages(publicKey: string) {
    let httpParam = new HttpParams();
    httpParam = httpParam.append('publicKey', publicKey);
    return this.http.get(LoginService.CLIENT_AD + 'messages', {params: httpParam});
  }

  getRooms() {
    return this.http.get(LoginService.CLIENT_AD + 'rooms');
  }
  getRoom(publicKey: string) {
    let httpParam = new HttpParams();
    httpParam = httpParam.append('publicKey', publicKey);
    return this.http.get(LoginService.CLIENT_AD + 'rooms', {params: httpParam});
  }
  deleteRoom(publicKey: string) {
    let httpParam = new HttpParams();
    httpParam = httpParam.append('publicKey', publicKey);
    return this.http.delete(LoginService.CLIENT_AD + 'rooms', {params: httpParam});
  }
  createRoom(input: RoomModel) {
    let ob = this.http.post(LoginService.CLIENT_AD + 'rooms', input);
    // Notify others in the group
    for (const p of input.participants) {
      if (p.publicKey !== LoginService.sSession.publicKey) {
        const url = 'http://' + p.ip + '/';
        // Construct the http
        ob = ob.pipe(switchMap(() => this.http.post( url + 'rooms', input)));
      }
    }
    return ob;
  }
  sendMessage(message: MessageModel, room: RoomModel) {
    let httpParam = new HttpParams();
    httpParam = httpParam.append('publicKey', room.publicKey);
    this.http.post(LoginService.CLIENT_AD + 'messages', message).pipe(switchMap((result: MessageModel) => {
      message = result;
      return this.http.post(LoginService.CLIENT_AD + 'receiveMessage', result, {params: httpParam});
    })).subscribe();
    // TODO p.ip filter user in the group
    // for (const p of room.participants) {
    //   if (p.publicKey !== LoginService.sSession.publicKey) {
    //   }
    // }
  }
}
