import {Component, OnInit} from '@angular/core';
import {RoomModel} from '../models/room.model';
import {fadeIn} from '../data-animation';
import {MessageService} from '../message.service';
import {LoginService} from '../login.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-chat-rooms',
  templateUrl: './chat-rooms.component.html',
  styleUrls: ['./chat-rooms.component.css'],
  animations: [fadeIn]
})
export class ChatRoomsComponent implements OnInit {
  rooms: RoomModel[];

  constructor(
    private messageService: MessageService,
    private login: LoginService,
    private route: ActivatedRoute
  ) {
  }

  ngOnInit() {
    const client = this.route.snapshot.queryParamMap.get('local');
    if (client) {
      LoginService.CLIENT_IP = client;
      LoginService.CLIENT_AD = 'http://' + client + '/';
    }
    this.login.getSession();

    this.messageService.getRooms().subscribe((result: RoomModel[]) => this.rooms = result);
  }

  delete(rm: RoomModel) {
    this.messageService.deleteRoom(rm.publicKey).subscribe(() => {
      this.ngOnInit();
    });
  }
}
