import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {ParticipantModel} from './models/participant.model';
import {catchError} from 'rxjs/operators';
import {throwError} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  public static CLIENT_IP = '';
  public static CLIENT_AD = '';
  public static sOnlineServer: string;
  public static sSession: ParticipantModel;
  get sSession() {
    return LoginService.sSession;
  }

  constructor(
    private http: HttpClient,
  ) { }

  getSession() {
    this.http.get(LoginService.CLIENT_AD).subscribe((result: {onlineApi: string, session: any}) => {
      LoginService.sOnlineServer = result.onlineApi;
      LoginService.sSession = result.session;
    });
  }

  getPeople() {
    return this.http.get(LoginService.sOnlineServer);
  }
}
