import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ParticipantModel} from '../models/participant.model';
import {LoginService} from '../login.service';
import {MatDialogRef, MatListOption, MatSelectionList} from '@angular/material';
import {MessageService} from '../message.service';
import {RoomModel} from '../models/room.model';

@Component({
  selector: 'app-create-dialog',
  templateUrl: './create-dialog.component.html',
  styleUrls: ['./create-dialog.component.css']
})
export class CreateDialogComponent implements OnInit {
  @ViewChild('peopleSelection', {static: true}) peopleSelection: MatSelectionList;
  @ViewChild('imageCanvas', {static: true}) imageCanvas: ElementRef<HTMLCanvasElement>;
  input = new RoomModel();
  people: ParticipantModel[];
  isCreating = false;
  constructor(
    private login: LoginService,
    private messageService: MessageService,
    private dialogRef: MatDialogRef<CreateDialogComponent>
  ) { }

  ngOnInit() {
    this.login.getPeople().subscribe((result: ParticipantModel[]) => {
      this.people = result.filter(p => p.publicKey !== this.login.sSession.publicKey);
      console.log(this.login.sSession);
    });
  }
  changeFile(event) {
    const file = event.target.files[0];
    const fr = new FileReader();
    fr.addEventListener('load', (e: any) => {
      this.input.img = e.target.result;
    });
    fr.readAsDataURL(file);
  }
  submit() {
    this.input.participants = this.peopleSelection.selectedOptions.selected.map((o: MatListOption) => o.value);
    this.messageService.createRoom(this.input);
    this.dialogRef.close();
  }
}
